<template>
  <q-page class="my-children layout-padding">
    <p style="margin-top: 10px">
      It is essential Juggle Street helpers know the number of children you have and how old they are. We ask for the birth month and year so your childs age is always up-to-date. Only your childs age is displayed, not the birth month.
    </p>
    <q-list v-if="children.length > 0">
      <q-item
        v-for="(child, index) in children"
        :key="index"
      >
        <q-item-section top avatar>
          <q-avatar icon="child_care" size="xl" />
        </q-item-section>
        <q-item-section>
          <q-item-label>
            {{child.firstName}} - {{child.ageInYears}} yr{{child.ageInYears === 1 ? '' : 's'}}
          </q-item-label>
        </q-item-section>
        <q-item-section>
          <q-item-label>
            <q-btn
              size="sm"
              round
              color="primary"
              icon="create"
              class="q-mx-sm"
              @click="handleEditChild(child, index)"
            />
            <q-btn
              size="sm"
              round
              class="q-mx-sm bg-secondary-lighter text-white"
              icon="close"
              @click="handleDeleteChild(index)"
            />
          </q-item-label>
        </q-item-section>
      </q-item>
    </q-list>

    <div class="text-center">
      <q-btn @click="handleAddChild" color="accent">
        Add Child
      </q-btn>
    </div>
    <small class="text-faded" v-if="children.length === 0">* required</small>

    <add-child-modal ref="addChildModal" v-on:addChildSuccess="handleAddChildSuccess" />
  </q-page>
</template>

<script>
import AddChildModal from 'components/AddChildModal';
import { mapState, mapActions } from 'vuex';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountMyChildren',
  components: { AddChildModal },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleEditChild(child, index) {
      this.$refs.addChildModal.open({ ...child }, index);
    },
    handleAddChild() {
      this.$refs.addChildModal.open({ dateOfBirth: new Date() }, -1);
    },
    handleDeleteChild(index) {
      const newChildren = [...this.children];

      if (index >= 0) {
        newChildren.splice(index, 1);
      }

      this.setProperty('children', newChildren);
      this.updateUserInfo().catch((error) => {
        NotificationService.negative(error.message);
      });
    },
    handleAddChildSuccess({ child, index }) {
      const newChildren = [...this.children];

      if (index >= 0) {
        newChildren[index] = child;
      } else {
        newChildren.push(child);
      }

      this.setProperty('children', newChildren);
      this.updateUserInfo().catch((error) => {
        NotificationService.negative(error.message);
      });
    },
  },
  computed: {
    ...mapState({
      children: (state) => state.userInfo.data.children,
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
};
</script>
<style lang="sass">
.my-children
  .add-child-button
    margin-top: 1rem
  .icons
    display: block
</style>
